import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StopChecksJourneyService } from './services/stop-checks-journey-config.service';
declare const $localize: any;

@Component({
  selector: 'bb-stop-checks-journey',
  templateUrl: './stop-checks-journey.component.html',
})
export class StopChecksJourneyComponent {
  readonly heading = $localize`:@@stop-checks-journey.title:Stop Checks`;
  readonly stopChecksCreate = $localize`:@@stop-checks-journey.create-checks-button:New request`;

  constructor(
    readonly config: StopChecksJourneyService,
    private readonly route: ActivatedRoute,
    private readonly router: Router
  ) {}

  navigateToStopChecksCreate() {
    this.router.navigate(['form'], { relativeTo: this.route.firstChild });
  }
}
